.game-container {
  text-align: center;
}

.image-container img {
  width: 300px;
  height: auto;
  margin: 20px;
}

.guess-input input {
  padding: 10px;
  font-size: 16px;
  margin-right: 10px;
}

button {
  padding: 10px;
  font-size: 16px;
  cursor: pointer;
}

.message {
  font-size: 18px;
  font-weight: bold;
  color: green;
}

.message.wrong {
  color: #FF0000;
}
